$(document).ready(function () {
  
    //Burger
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    //Select
    $(function() {
        $(".select-hidden .select-item").click(function(e) {
            e.stopPropagation();
            let $item = $(this);
            let html = $item.html();
            $item.closest(".select").find(".select-current").html(html);
            $item.closest(".select").removeClass("select--active");
            $item.siblings(".select-item").removeClass("select-item--active");
            $item.addClass("select-item--active");
        });

        $(".select-current").on("click", function(e) {
            e.stopPropagation();
            let $sel = $(this).closest(".select");
            $(".select").not($sel).removeClass("select--active");
            $sel.toggleClass("select--active");
        });

        $(".select").on("mouseleave", function() {
            $(this).removeClass("select--active");
        });

        $(document).on("click", function(e) {
            if (!$(e.target).closest(".select").length) {
                $(".select").removeClass("select--active");
            }
        });

        var mq = window.matchMedia('(max-width: 992px)');

        function bindSelectShow() {
            $('.select-show').off('.selectShow');

            $(".select").removeClass("select--active");

            if (mq.matches) {
                $('.select-show').on('click.selectShow', function(e) {
                    e.stopPropagation();
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.toggleClass("select--active");
                });
            } else {
                $('.select-show').on('mouseenter.selectShow', function(e) {
                    var $sel = $(this).closest('.select');
                    $(".select").not($sel).removeClass("select--active");
                    $sel.addClass("select--active");
                });
            }
        }

        bindSelectShow();
        if (mq.addEventListener) {
            mq.addEventListener('change', bindSelectShow);
        } else if (mq.addListener) { 
            mq.addListener(bindSelectShow);
        }
    });

    //Calendar
    new AirDatepicker('#range', {
        range: true,
        multipleDatesSeparator: ' - ',
        inline: true,
    });

     const $calendar = $('.calendar');

    function addActive() {
        $calendar.addClass('calendar--active');
    }

    function removeActive() {
        $calendar.removeClass('calendar--active');
    }

    function handleClickOutside(e) {
        if (!$calendar.is(e.target) && $calendar.has(e.target).length === 0) {
            removeActive();
        }
    }

    const isDesktop = window.matchMedia('(min-width: 993px)');

    function handleMediaChange() {
        $calendar.off('mouseenter mouseleave click');
        $(document).off('click', handleClickOutside);

        if (isDesktop.matches) {
            $calendar.hover(addActive, removeActive);
        } else {
            $calendar.on('click', addActive);
            $(document).on('click', handleClickOutside);
        }
    }

    isDesktop.addEventListener('change', handleMediaChange);

    handleMediaChange();


})